
/* –ó–∞–¥–∞–Ω–∏–µ –Ω–∞ —É—Ä–æ–∫:

1) –ü–µ—Ä–≤—É—é —á–∞—Å—Ç—å –∑–∞–¥–∞–Ω–∏—è –ø–æ–≤—Ç–æ—Ä–∏—Ç—å –ø–æ —É—Ä–æ–∫—É

2) –°–æ–∑–¥–∞—Ç—å —Ñ—É–Ω–∫—Ü–∏—é showMyDB, –∫–æ—Ç–æ—Ä–∞—è –±—É–¥–µ—Ç –ø—Ä–æ–≤–µ—Ä—è—Ç—å —Å–≤–æ–π—Å—Ç–≤–æ privat. –ï—Å–ª–∏ —Å—Ç–æ–∏—Ç –≤ –ø–æ–∑–∏—Ü–∏–∏
false - –≤—ã–≤–æ–¥–∏—Ç –≤ –∫–æ–Ω—Å–æ–ª—å –≥–ª–∞–≤–Ω—ã–π –æ–±—ä–µ–∫—Ç –ø—Ä–æ–≥—Ä–∞–º–º—ã

3) –°–æ–∑–¥–∞—Ç—å —Ñ—É–Ω–∫—Ü–∏—é writeYourGenres –≤ –∫–æ—Ç–æ—Ä–æ–π –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—å –±—É–¥–µ—Ç 3 —Ä–∞–∑–∞ –æ—Ç–≤–µ—á–∞—Ç—å –Ω–∞ –≤–æ–ø—Ä–æ—Å 
"–í–∞—à –ª—é–±–∏–º—ã–π –∂–∞–Ω—Ä –ø–æ–¥ –Ω–æ–º–µ—Ä–æ–º ${–Ω–æ–º–µ—Ä –ø–æ –ø–æ—Ä—è–¥–∫—É}". –ö–∞–∂–¥—ã–π –æ—Ç–≤–µ—Ç –∑–∞–ø–∏—Å—ã–≤–∞–µ—Ç—Å—è –≤ –º–∞—Å—Å–∏–≤ –¥–∞–Ω–Ω—ã—Ö
genres

P.S. –§—É–Ω–∫—Ü–∏–∏ –≤—ã–∑—ã–≤–∞—Ç—å –Ω–µ –æ–±—è–∑–∞—Ç–µ–ª—å–Ω–æ*/

// –ö–æ–¥ –≤–æ–∑—å–º–∏—Ç–µ –∏–∑ –ø—Ä–µ–¥—ã–¥—É—â–µ–≥–æ –¥–æ–º–∞—à–Ω–µ–≥–æ –∑–∞–¥–∞–Ω–∏—è

// –≥–ª–∞–≤–Ω—ã–π –æ–±—ä–µ–∫—Ç –ø—Ä–æ–≥—Ä–∞–º–º—ã
const personalMovieDB = {
    count: null,
    movies: {},
    actors: {},
    genres: [],
    privat: true,
}
function start(obj) {

    let movNum = prompt('–°–∫–æ–ª—å–∫–æ —Ñ–∏–ª—å–º–æ–≤ –≤—ã —É–∂–µ –ø–æ—Å–º–æ—Ç—Ä–µ–ª–∏?', '');
    while (isNaN(movNum) || movNum === null || movNum === '') {
        movNum = prompt('–°–∫–æ–ª—å–∫–æ —Ñ–∏–ª—å–º–æ–≤ –≤—ã —É–∂–µ –ø–æ—Å–º–æ—Ç—Ä–µ–ª–∏?', '');
    }
    obj['count'] = movNum;

}
start(personalMovieDB);

// –î–æ–±–∞–≤–ª—è–µ—Ç moviesNum —Å–≤–æ–π—Å—Ç–≤ –∫ —Å–≤–æ–π—Å—Ç–≤—É ['movies'] –∫ –æ–±—ä–µ–∫—Ç—É obj
let addMovies = (obj, moviesNum) => {
    for (let i = 0; i < moviesNum; i++) {
        let movieName = prompt('–û–¥–∏–Ω –∏–∑ –ø–æ—Å–ª–µ–¥–Ω–∏—Ö –ø—Ä–æ—Å–º–æ—Ç—Ä–µ–Ω–Ω—ã—Ö —Ñ–∏–ª—å–º–æ–≤?');
        // –ø—Ä–æ–≤–µ—Ä–∫–∞ –Ω–∞ –æ—à–∏–±–∫—É –≤–≤–æ–¥–∞
        while (movieName === null || movieName === '') {
            movieName = prompt('–û–¥–∏–Ω –∏–∑ –ø–æ—Å–ª–µ–¥–Ω–∏—Ö –ø—Ä–æ—Å–º–æ—Ç—Ä–µ–Ω–Ω—ã—Ö —Ñ–∏–ª—å–º–æ–≤?');
        }
        let movieScore = prompt('–ù–∞ —Å–∫–æ–ª—å–∫–æ –æ—Ü–µ–Ω–∏—Ç–µ –µ–≥–æ?');
        // –ø—Ä–æ–≤–µ—Ä–∫–∞ –Ω–∞ –æ—à–∏–±–∫—É –≤–≤–æ–¥–∞
        while (isNaN(movieScore) || movieScore === null || movieScore === '' || movieScore < 0 || movieScore > 10) {
            movieScore = prompt('–ù–∞ —Å–∫–æ–ª—å–∫–æ –æ—Ü–µ–Ω–∏—Ç–µ –µ–≥–æ?');
        }
        obj['movies'][movieName] = movieScore;
    }
};

addMovies(personalMovieDB, 2);


function showMyDB(hidden, obj) {
    !hidden ? console.log(obj) : '';
}

showMyDB(personalMovieDB['privat'], personalMovieDB);

function writeYourGenres(obj) {
    for (let genreNumber = 1; genreNumber <= 3; genreNumber++) {
        let genre = prompt(`–í–∞—à –ª—é–±–∏–º—ã–π –∂–∞–Ω—Ä –ø–æ–¥ –Ω–æ–º–µ—Ä–æ–º ${genreNumber}`);
        while (!isNaN(genre) || genre === null || genre === '') {
            genre = prompt(`–í–∞—à –ª—é–±–∏–º—ã–π –∂–∞–Ω—Ä –ø–æ–¥ –Ω–æ–º–µ—Ä–æ–º ${genreNumber}`);
        }
        obj['genres'][genreNumber - 1] = genre;
    }
}

writeYourGenres(personalMovieDB);
console.log(personalMovieDB['genres']);